@font-face {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  src: local("Inter Regular"),
    local("Inter-Regular"),
    url("./inter/regular/Inter-Regular.woff") format("woff"),
    url("./inter/regular/Inter-Regular.woff2") format("woff2");
}

