  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  src: local("Inter Medium"),
    local("Inter-Medium"),
    url("./inter/medium/Inter-Medium.woff") format("woff"),
    url("./inter/medium/Inter-Medium.woff2") format("woff2");
